.header {
  width: 100%;
  max-width: 1440px;
  padding-inline: 90px;
}
@media (max-width: 768px) {
  .header {
    padding-inline: 32px;
  }
}

.nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1;
}
@media (max-width: 768px) {
  .logo {
    padding-block: 25px;
  }
}

.logo__img {
  width: 44px;
  height: 44px;
}
@media (max-width: 768px) {
  .logo__img {
    width: 38px;
    height: 38px;
  }
}

.logo__text {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 100%;
  letter-spacing: 0px;
}
@media (max-width: 768px) {
  .logo__text {
    font-size: 22px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding-block: 36px;
}
@media (max-width: 768px) {
  .nav__list {
    display: none;
  }
}

.nav__link {
  font-family: "Sora";
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px;
  color: #88888d;
}
.nav__link:hover {
  color: #ffffff;
}

.nav__link--template {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 14px;
  line-height: 20px;
  padding-block: 14px;
  padding-inline: 24px;
  border-radius: 8px;
  background-color: #222233;
}

.burger {
  display: none;
  width: 26px;
  height: 18px;
}
@media (max-width: 768px) {
  .burger {
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 6px;
    cursor: pointer;
  }
}
.burger::before, .burger::after,
.burger span::before {
  position: absolute;
  content: "";
  width: 26px;
  height: 2px;
  background-color: #ffffff;
}
.burger::before {
  top: 0;
}
.burger span::before {
  bottom: 0;
}
.burger::after {
  bottom: 8px;
}